import { Injectable } from '@angular/core';
import { Http, RequestOptions,Response, Headers } from '@angular/http';
import { HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map'

@Injectable()
export class FacesService {

  constructor(private http:Http) { }

  // server='http://localhost:3000/';
  server= 'http://ec2-13-229-128-0.ap-southeast-1.compute.amazonaws.com:3000/';
  /*Add a face to a face list*/
  addFace(data) {
	  return this.http.post( this.server + 'api/faces/', data).map(res => res.json());
  }
  
  /*find a similar face in the face list*/
  findSimilar(data){
	  return this.http.post( this.server + 'api/faces/find', data).map(res=>res.json());
  }
  
  /*Show all the avaliable faces*/
  getAllFaces(){
    console.log(`gettting all faces...`)
	  return this.http.get( this.server + 'api/faces/').map(res => res.json());
  }
  
  /*Delete the selected face*/
  deleteFace(id){
    console.log('checking2');
    return this.http.delete( this.server + 'api/faces/'+id).map(res => res.json());
  }
}



